*{
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}

.container{
    width:100%;
    position:relative;
}

.img1{
    width:100%;
    max-height:100vh;
}

.box1{
    position:absolute;
    top:30%;
    left:10%;
    width:24%;
}

.box1 h1{
    color:white;
    font-weight: 500;
    font-size: 1.3rem;
    text-align: center;
    letter-spacing: 0.4px;
    word-spacing: 1.8px;
}

.logo1{
    width:100%;
}

.span1{
    color:rgb(44, 160, 255);
}

.div-btn{
    display:flex;
    width:100%;
}

.btn{
    font-size: 1.4rem;
    border:none;
    border-radius: 6px;
    padding:2px;
    font-weight: 700;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
}

.btn1, .btn2{
    width:50%;
    margin:8px 2px;
}

.btn1:hover{
    background-color: goldenrod;
    transition: 0.3s;
}

.btn2{
    background-color: goldenrod;
}

.btn1, .box1 h1{
    animation: fadeInAnimation ease 2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}



/* ########################## Page 2 #################################### */

.container2{
    height:85%;
    margin-top:20px;
    width: 60vw;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 150%;
    left: 50%;
    overflow: hidden;
    border-radius: 8px;
    border: solid transparent;
}

.wrapper{
    width: 100%;
    display: flex;
    animation: slide 10s infinite;
}

@keyframes slide{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(-400%);
    }
}

.wrapper .idk{
    width: 100%;
    height:100%;

    object-fit: contain;
}

.conp{
    text-align: center;
    font-weight: 700;
    font-size: 2rem;
    padding-top:2%;
    cursor: pointer;
}

.con:hover .fgg{
    color:rgb(44, 160, 255);
    transition: 0.5s;
}

.con:hover .container2{
    box-shadow: 10px 25px 30px rgba(30,30,200,0.3);
    transition: 0.5s;
}

.con{
    margin-bottom:55%;
}



/* ######################## Page 3 ############################## */


.container3{
    background-color: black;
    color:white;
    text-align: center;
    padding:3% 0;
    font-size: 1.8rem;
}

.do-span{
    color:goldenrod;
}



/* ################################## Page 4 ################################# */

.container4{
    display:flex;
    font-size: 1.8rem;
    width:100%;
    padding:8% 20%;
    flex-wrap: wrap;
}

.box41, .box42{
    width:50%;
    min-height: 1vh;
    padding-bottom:3%;
    font-weight: 500;
}

.ex-btn1{
    background-color: gray;
}

.ex-btn1, .ex-btn2{
    padding:2% 0;
    margin:0 1%;
}

.box42{
    color:rgb(1, 140, 253);
}

.container4:hover .spm{
    color:goldenrod;
}



/* ############################ For Responsive ############################### */

@media(max-width:500px){
    *{
        background-color: #f1f4ff;
    }
    
    .container{
        width:100vw
    }
    
    .box1{
        position:unset;
        width:80%;
    }

    .p-box1{
        display:flex;
        justify-content: center;
    }

    .logo1{
        padding-top:30px;
    }

    .box1 h1{
        color:black;
        font-weight: 500;
        font-size: 1.3rem;
        padding:10px 0;
    }

    .div-btn{
        flex-direction: column;
    }

    .btn1, .btn2{
        width:100%;
        margin:8px 2px;
        padding:10px 0;
    }

    .btn{
        font-size: 1.7rem;
        font-weight: 700;
        border-radius: 10px;
    }

    .btn1{
        margin-top:80px;
    }

    .btn1:hover{
        background-color: lightgray;
        transition: 0.3s;
    }

    .container2{
        height:24%;
    }

    @keyframes slide{
        0%{
            transform: translateX(0);
        }
        25%{
            transform: translateX(-100%);
        }
        50%{
            transform: translateX(-200%);
        }
        75%{
            transform: translateX(-300%);
        }
        100%{
            transform: translateX(-400%);
        }
    }
    
    .container2{
        top:120%;
        width:80vw;
        height:28%;
    }

    .conp{
        font-size:1.1rem;
    }

    .container3{
        margin-top:150%;
    }

    .container3 h1 ,.do-span{
        background-color: black;
    }

    .box41, .box42{
        width:100%;
    }

    .box41, .ex-btn1{
        display:none;
    }

    .box42{
        font-size: 1.8rem;
        padding-top:50%;
        padding-bottom:10%;
    }

    .div-btn{
        padding-bottom:10%;
    }
}